<template>
  <v-container fluid class="down-top-padding">
    <v-row>
       <input type="file" name="" class="d-none"  ref="files" @change="fileInput" id="">
      <v-col cols="12" lg="4">
        <v-card>
          <v-card-text class="text-center pa-7">
            <v-list-item-avatar size="170" color="grey darken-3">
          <v-img
            v-if="colorShow"
            class="elevation-6"
            :src="getSchoolData.image==''?'https://s3.amazonaws.com/37assets/svn/765-default-avatar.png':image!=''?image:getSchoolData.image"
             ></v-img>
        </v-list-item-avatar>
        <p class="text-danger">{{errImg}}</p>
              <div class="text-center">
            <v-btn class="text-capitalize mt-5 element-0" style="color:white"  @click="$refs['files'].click()" color="danger"> + Resim {{getSchoolData.image!=''?'Düzenle':'Ekle'}}</v-btn>
  </div>
  <div class="text-center text danger" style="margin-top:10px;color:white">{{errImage}}</div>
            <h4 class="mt-2 title blue-grey--text text--darken-2 font-weight-regular">{{getSchoolData.name}}</h4>
            <h6 class="subtitle-2 font-weight-light">{{'Okul Adı'}}</h6>
          </v-card-text>
        </v-card>
      </v-col>
      <v-col cols="12" lg="8">
        <v-card>
          <v-card-text>
            <h3 class="title blue-grey--text text--darken-2 font-weight-regular">{{$t('dashboard.insertTeacher')}}</h3>
            <h6 class="subtitle-2 font-weight-light">{{$t('teacher.info')}}</h6>
          </v-card-text>
          <v-divider></v-divider>
          <v-card-text>
            <v-text-field
              v-model="getSchoolData.name"
              :error-messages="errName"
              :label="'Okul Adı'"
              filled
              background-color="transparent"
            ></v-text-field>
               <v-text-field
              v-model="getSchoolData.phone"
              :error-messages="errPhone"
               :label="'Telefon'"
              filled
              background-color="transparent"
            ></v-text-field>
            <v-text-field
              type="text"
              v-model="getSchoolData.address"
                :label="'Okul Adresi'"
              filled
              :success-messages="successmessages"
              :error-messages="errormessages"
              background-color="transparent"
            ></v-text-field>
            <v-row justify="space-around">
             <div>
             <p>Header Rengi : {{headerColor}}</p>
             <v-color-picker v-model="headerColor" show-swatches swatches-max-height="300px"></v-color-picker>
             </div>
             <div>
             <p>Yan Menü Rengi : {{sideBarColor}}</p>
             <v-color-picker v-model="sideBarColor" show-swatches swatches-max-height="300px"></v-color-picker>
             </div>
            </v-row>
            <v-divider></v-divider>
            <!-- <v-row class="mt-3" justify="space-around">
             <div>
             <p>1. Alternatif Buton Rengi : {{primaryColor}}</p>
             <v-color-picker v-model="primaryColor" show-swatches swatches-max-height="300px"></v-color-picker>
             </div>
             <div>
             <p>2. Alternatif Buton Rengi : {{successColor}}</p>
             <v-color-picker v-model="successColor" show-swatches swatches-max-height="300px"></v-color-picker>
             </div>
             <div>
             <p>3. Alternatif Buton Rengi : {{infoColor}}</p>
             <v-color-picker v-model="sideBarColor" show-swatches swatches-max-height="300px"></v-color-picker>
             </div>

            </v-row> -->
            <v-btn class="text-capitalize mt-5 element-0" @click="sendUpdateSchool"  color="success">{{$t('generate.submit')}}</v-btn>
          </v-card-text>
        </v-card>
      </v-col>
      <snack-bar :text="textMsg" :snackbar="snackbar" @finished="snackbar=$event"></snack-bar>
    </v-row>
      <loading-dialog :dialogLoading="dialogLoading">Okul bilgilerini değiştirdiniz! Sizi giriş ekranına yönlendiriyoruz. Tekrardan giriş yaptığınızda bilgileriniz güncellenecektir.</loading-dialog>
  </v-container>
</template>

<script>
import loadingDialog from '../../components/dialogs/loadingDialogs'
import { mapGetters } from 'vuex'
export default {
  components:{
    loadingDialog
  },
    computed:{
        ...mapGetters(['getSchoolData'])
    },
    watch:{
        headerColor(data){
            this.$store.state.user.schoolData.headerColor = data
        },
        sideBarColor(data){
            this.$store.state.user.schoolData.sideBarColor = data
        },
         successColor(data){
            this.$store.state.user.schoolData.successColor = data
        },
        sideBarButtonActiveColor(data){
            this.$store.state.user.schoolData.sideBarButtonActiveColor = data
        },
        infoColor(data){
            this.$store.state.user.schoolData.infoColor = data
        },
        primaryColor(data){
            this.$store.state.user.schoolData.primaryColor = data
        }
    },
    data(){
        return{
          dialogLoading:false,
            errName:[],
            errLastname:[],
            errormessages:[],
            successmessages:[],
            headerColor:"",
            setHeaderColor:"",
            sideBarColor:"",
            setSideBarColor:"",
            sideBarButtonActiveColor:"",
            errImg:"",
            errPhone:[],
            primaryColor:"",
            infoColor:"",
            successColor:"",
           colorShow:true,
            errImage:'',
            color:"",
             swatches: [
        ['#FF0000', '#AA0000', '#550000'],
        ['#FFFF00', '#AAAA00', '#555500'],
        ['#00FF00', '#00AA00', '#005500'],
        ['#00FFFF', '#00AAAA', '#005555'],
        ['#0000FF', '#0000AA', '#000055'],
      ],
      newImage:null,
      image:"",
      textMsg:"",
      snackbar:false,
      updated:false
        }
    },
    methods:{
    createImage(file) {

      var reader = new FileReader();
      var vm = this;
      reader.onload = e => {
        vm.image = e.target.result;
        this.$store.state.user.schoolData.image = '1'
             this.colorShow=true
      };
      reader.readAsDataURL(file);


    },
    fileInput(event){
      this.colorShow=false
       this.newImage = event.target.files[0]
       this.createImage(this.newImage)
    },
        sendUpdateSchool(){
            if(this.getSchoolData.name==''||this.getSchoolData.phone.length<9||this.getSchoolData.address==''||this.getSchoolData.image==''||(this.newImage==null&&this.getSchoolData.image=='')){
                if(this.getSchoolData.name==''){
                    this.errName.push('Okul adı boş geçilemez.')
                }
                if(this.getSchoolData.phone.length<9){
                    this.errPhone.push('Eksik veya geçersiz telefon numarası girdiniz.')
                }
                if(this.getSchoolData.address==''){
                    this.errormessages.push('Adres kısmı boş geçilemez.')
                }
                if(this.getSchoolData.image==''||this.newImage==null){
                    this.errImg = 'Okulunuza Resim ekleyiniz.'
                }
            }
            else{
                if(this.image==''){
                    this.$api.put('school',this.getSchoolData.id,{
                        name: this.getSchoolData.name,
                        address:this.getSchoolData.address,
                        phone:this.getSchoolData.phone,
                        headerColor: this.getSchoolData.headerColor,
                        sideBarColor:this.getSchoolData.sideBarColor
                    }).then((res)=>{
                      if(res.result==1)
                      {
                        this.textMsg="İşleminiz başarıyla gerçekleşti."
                       this.snackbar=true
                       this.updated=true
                       this.dialogLoading=true
                       setTimeout(() => {
                         this.dialogLoading=false
                         localStorage.clear()
                         this.$router.push('/login')
                       }, 2000);

                      }
                      else
                       {
                        this.textMsg="Okulunuzda herhangi bir değişiklik yapılmadı."
                          this.snackbar=true
                       }
                    })
                }
                else{
                    let fd = new FormData()
                    fd.append('name',this.getSchoolData.name)
                    fd.append('address',this.getSchoolData.address)
                    fd.append('phone',this.getSchoolData.phone)
                    fd.append('headerColor',this.getSchoolData.headerColor)
                    fd.append('sideBarColor',this.getSchoolData.sideBarColor)
                    fd.append('image',this.newImage)

                    this.$api.putfd('school',this.getSchoolData.id,fd).then((res)=>{
                       if(res.status=='success')
                      {
                        this.textMsg="İşleminiz başarıyla gerçekleşti."
                       this.snackbar=true
                       this.updated=true
                      }
                      else
                       {
                        this.textMsg="Okulunuzda herhangi bir değişiklik yapılmadı."
                          this.snackbar=true
                       }

                    })
                }
            }
        },
    },
    beforeRouteLeave(from,to,next){
      if(!this.updated){
        this.headerColor = this.setHeaderColor
        this.sideBarColor = this.setSideBarColor
      }
      setTimeout(() => {
        next()
      }, 50);
    },
    mounted(){
        this.headerColor = this.getSchoolData.headerColor
        this.setHeaderColor = this.getSchoolData.headerColor
        this.sideBarColor = this.getSchoolData.sideBarColor
        this.setSideBarColor = this.getSchoolData.sideBarColor
    }
}
</script>

